footer {
    position: fixed; 
    bottom: 0; 
    left: 0; 
    width: 100%; 
    background-color: rgba(0, 0, 0, 0.5); 
    color: white;
    padding: 10px;
    text-align: center; 
}
footer p {
    margin: 0; 
    font-size: 16px; 
}

@media(max-width: 768px) {
    footer {
        padding: 15px;
    }

    footer p {
        font-size: 18px;
        padding: 0 10px;
    }
}
